#resumeTitle {
  font-size: 35px; 
  text-align: center;
}

#resumeContainer {
  display: flex;
  flex-direction: row;
}

#resumeLeft {
  width: 60%;
}

#resumeRight {
  width: 40%;
  padding-left: 100px;
}

.sideTitle {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 35px;
  margin-top: 0px;
}

.school {
  font-weight: bold;
  color: gray;
  font-size: 25px;
}

.date {
  font-size: 20px;
  color: gray;
}

.edStint p {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 150%;
}

.edStint {
  margin-bottom: 80px;
}

.moreInfo {
  margin-left: 25px;
  font-size: 18px;
}

#accomplishments {
  margin-top: 100px;
}

.moreInfoRight {
  font-size: 20px;
  line-height: 120%;
  padding-left: 15px;
}